import { z } from "zod";
import type { PaymentsConfig } from "../types";
import { getAgentKitConfig } from "../config";

export type Hex = `0x${string}`;

export const ZERO_ADDRESS: Hex = "0x0000000000000000000000000000000000000000";

export function normalizeDomain(domain: string): string {
  return domain?.trim?.().toLowerCase?.() ?? "";
}

export function normalizeAddress(value: string | null | undefined): Hex {
  if (!value) {
    throw new Error("invalid hex address");
  }
  const trimmed = value.trim();
  if (!/^0x[0-9a-fA-F]{40}$/.test(trimmed)) {
    throw new Error(`invalid hex address: ${value}`);
  }
  return trimmed.toLowerCase() as Hex;
}

export function sanitizeAddress(value: string | null | undefined): Hex {
  if (!value) return ZERO_ADDRESS;
  try {
    return normalizeAddress(value);
  } catch {
    return ZERO_ADDRESS;
  }
}

export function toJsonSchemaOrUndefined(s?: z.ZodTypeAny) {
  if (!s) return undefined;
  try {
    return z.toJSONSchema(s);
  } catch {
    return undefined;
  }
}

export function paymentsFromEnv(params?: {
  defaultPrice?: string;
}): PaymentsConfig {
  const { payments } = getAgentKitConfig();
  return {
    payTo: payments.payTo,
    facilitatorUrl: payments.facilitatorUrl,
    network: payments.network,
    defaultPrice: params?.defaultPrice ?? payments.defaultPrice,
  };
}
